import { useCallback, useEffect, useState } from "react";
import Modal from "react-modal";

import { BigNumber } from "ethers";
import { WeightedMultiOwner } from "./contract";
import { partition, prop } from "ramda";

type Transaction = {
  index: number;
  createdBy: string;
  destination: string;
  value: BigNumber;
  data: string;
  executed: boolean;
  voteWeight: BigNumber;
  confirmedBySender: boolean;
};

/**
 * @example
 * // returns [0, 1, 2, 3, 4]
 * range(5)
 */
const range = (toExcluse: number) => [...Array(toExcluse).keys()];

function AddTransactionForm({
  contract,
  updateTransactions,
}: {
  contract: WeightedMultiOwner;
  updateTransactions: () => Promise<void>;
}) {
  const [showForm, setShowForm] = useState(false);
  const [savingForm, setSavingForm] = useState(false);
  const [destination, setDestination] = useState("");
  const [value, setValue] = useState("");
  const [data, setData] = useState("");
  return (
    <>
      <div className="col-1" style={{ paddingTop: "0px" }}>
        <button
          onClick={() => setShowForm(true)}
          className="btn btn-secondary btn-md btn1"
          style={{ width: "200px" }}
        >
          + Submit transaction
        </button>
      </div>
      <Modal
        isOpen={showForm}
        onRequestClose={() => setShowForm(false)}
        className="container-fluid txcont"
        style={{ content: { display: "flex", justifyContent: "center" } }}
      >
        <form
          onSubmit={(e) => {
            e.preventDefault();
            setSavingForm(true);
            contract
              .submitTransaction(destination.trim(), value.trim(), data.trim())
              .then((t) => t.wait())
              .finally(() => {
                setSavingForm(false);
                setShowForm(false);
              })
              .then(updateTransactions);
          }}
        >
          <div className="row" style={{ textAlign: "center" }}>
            <label className="translabel"> Please fill in the following transaction data! </label>
          </div>
          <div className="row">
            <label className="translabel"> Destination </label>
            <input
              onChange={(e) => setDestination(e.target.value)}
              disabled={savingForm}
              pattern="\s*0[x][0-9a-fA-F]{40}\s*"
              placeholder="0xc02aaa39b223fe8d0a0e5c4f27ead9083c756cc2"
              required
              title="Ethereum address"
              type="text"
              value={destination}
            />
          </div>
          <div className="row">
            <label className="translabel">Value (Wei)</label>
            <input
              onChange={(e) => setValue(e.target.value)}
              disabled={savingForm}
              pattern="\s*\d+\s*"
              placeholder="0"
              required
              title="Non negative integer"
              type="string"
              value={value}
            />
          </div>
          <div className="row">
            <label className="translabel"> Data </label>
            <input
              className="transinp"
              onChange={(e) => setData(e.target.value)}
              disabled={savingForm}
              pattern="\s*0x([0-9a-fA-F][0-9a-fA-F])+\s*"
              placeholder="0x00"
              required
              title="Hexadecimal data with even number of digits"
              type="string"
              value={data}
            />
          </div>
          <div className="row" style={{ justifyContent: "center" }}>
            <input
              className="transbutton"
              type="submit"
              value={savingForm ? "Saving..." : "Save"}
              disabled={savingForm}
            />
          </div>
        </form>
      </Modal>
    </>
  );
}

const capitalize = (s: string) => s.charAt(0).toUpperCase() + s.slice(1);

type Transactions = {
  all: Transaction[];
  pending: Transaction[];
  executed: Transaction[];
  required: BigNumber;
  ownerWeights: (readonly [string, number])[];
};

type TransactionFilter = "all" | "pending" | "executed";

const TransactionTableHeader = ({ filter }: { filter: TransactionFilter }) => (
  <>
    <th className="th" scope="col">
      Information
    </th>
    <th className="th" scope="col">
      Destination
    </th>
    <th className="th" scope="col">
      Confirmation
    </th>
    {filter === "all" && (
      <th className="th" scope="col">
        Status
      </th>
    )}
    {filter !== "executed" && (
      <th className="th" scope="col">
        Action
      </th>
    )}
  </>
);

const TransactionTableRows = ({
  transactions,
  filter,
  contract,
  updateTransactions,
  setTransactions,
}: {
  transactions: Transactions;
  filter: TransactionFilter;
  contract: WeightedMultiOwner;
  updateTransactions: () => Promise<void>;
  setTransactions: (n: null) => void;
}) => (
  <>
    {transactions[filter].map((t) => (
      <tr key={t.index}>
        <td style={{ maxWidth: "400px", maxHeight: "400px" }}>
          <div className="row">
            <div className="col-sm-4" style={{ border: "none" }}>
              <div className="title bold">Transaction id:</div>
            </div>
            <div className="col-sm-6" style={{ border: "none" }}>
              <div className="record">{t.index}</div>
            </div>
          </div>
          <div className="row">
            <div className="col-sm-4" style={{ border: "none" }}>
              <div className="title bold">Transaction value:</div>
            </div>
            <div className="col-sm-6" style={{ border: "none" }}>
              <div className="record">{t.value.toString()} ILG</div>
            </div>
          </div>
          <div className="row">
            <div className="col-sm-4" style={{ border: "none" }}>
              <div className="title bold">Data:</div>
            </div>
            <div className="col-sm-6" style={{ border: "none" }}>
              <div className="record">{t.data}</div>
            </div>
          </div>
        </td>
        <td>
          <div className="row record bold dest">Destination:</div>
          <div className="row record dest">{t.destination}</div>
          <div className="row record bold dest">Submitted by:</div>
          <div className="row record dest">{t.createdBy}</div>
        </td>
        <td className="td1">
          {t.voteWeight.toString()}
          {t.executed ? "" : `/${transactions.required.toString()}`}
        </td>
        {filter === "all" && <td className="td1">{t.executed ? "Executed" : "Pending"}</td>}
        {filter !== "executed" && (
          <td className="td1">
            {!t.executed &&
              (t.confirmedBySender ? (
                <button
                  className="btn btn-primary btn-md"
                  onClick={() => {
                    setTransactions(null);
                    contract
                      .revokeConfirmation(t.index)
                      .then((t) => t.wait())
                      .finally(updateTransactions);
                  }}
                >
                  - Revoke
                </button>
              ) : (
                <button
                  className="btn btn-primary btn-md"
                  onClick={() => {
                    setTransactions(null);
                    contract
                      .confirmTransaction(t.index)
                      .then((t) => t.wait())
                      .finally(updateTransactions);
                  }}
                >
                  + Confirm
                </button>
              ))}
          </td>
        )}
      </tr>
    ))}
  </>
);

export default function ({ contract, owners }: { contract: WeightedMultiOwner; owners: string[] }) {
  const [transactions, setTransactions] = useState<null | {
    all: Transaction[];
    pending: Transaction[];
    executed: Transaction[];
    required: BigNumber;
    ownerWeights: (readonly [string, number])[];
  }>(null);
  const updateTransactions = useCallback(
    (): Promise<void> =>
      Promise.all([
        contract.required(),
        contract
          .getTransactionsLength()
          .then((count) =>
            Promise.all(
              range(count.toNumber()).map((index) =>
                contract.getTransaction(index).then((t) => ({ ...t, index }))
              )
            )
          ),
        Promise.all(
          owners.map((owner) =>
            contract.getOwnerWeight(owner).then((weight) => [owner, weight] as const)
          )
        ),
      ]).then(([required, all, ownerWeights]) => {
        const [executed, pending] = partition(prop("executed"), all);
        setTransactions({ all, pending, executed, required, ownerWeights });
      }),
    [contract, owners]
  );
  useEffect(() => {
    updateTransactions().then();
  }, [updateTransactions]);

  type Tab = TransactionFilter | "owners";
  const [filter, setFilter] = useState<Tab>("all");
  const FilterTab = ({ _filter }: { _filter: Tab }) =>
    transactions && (
      <div
        className={"col-1" + (filter === _filter ? " chosen" : "")}
        onClick={() => setFilter(_filter)}
      >
        {capitalize(_filter)}
        {_filter !== "owners" && <>({transactions[_filter].length})</>}
      </div>
    );
  return (
    <>
      <div
        className="row justify-content-start trans"
        style={transactions === null ? { display: "none" } : {}}
      >
        <AddTransactionForm
          contract={contract}
          updateTransactions={() => {
            setTransactions(null);
            return updateTransactions();
          }}
        />
      </div>
      {transactions === null ? (
        <div>fetching transactions</div>
      ) : (
        <>
          <div className="row justify-content-start trans">
            <hr />
            <FilterTab _filter="all" />
            <FilterTab _filter="pending" />
            <FilterTab _filter="executed" />
            <FilterTab _filter="owners" />
            <hr />
          </div>
          <table className="table">
            <thead className="thead-dark thead">
              <tr>
                {filter === "owners" ? (
                  <>
                    <th className="th" scope="col">
                      Address
                    </th>
                    <th className="th" scope="col">
                      Weight
                    </th>
                  </>
                ) : (
                  <TransactionTableHeader filter={filter} />
                )}
              </tr>
            </thead>
            <tbody>
              {filter === "owners" ? (
                transactions.ownerWeights.map(([address, weight]) => (
                  <tr>
                    <td>{address}</td>
                    <td>{weight}</td>
                  </tr>
                ))
              ) : (
                <TransactionTableRows
                  contract={contract}
                  filter={filter}
                  setTransactions={setTransactions}
                  transactions={transactions}
                  updateTransactions={updateTransactions}
                />
              )}
            </tbody>
          </table>
        </>
      )}
    </>
  );
}
